import React, {useEffect} from 'react';
import {makeStyles, withStyles} from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import {compose} from "redux";
import {connect} from "react-redux";
import SignaturesAction from '../../redux/action/signaturesAction'
import CircularProgress from '@material-ui/core/CircularProgress';
import Chip from '@material-ui/core/Chip';
import TablePagination from '@material-ui/core/TablePagination';

const StyledTableCell = withStyles((theme) => ({
    head: {
        backgroundColor: '#D7DBDD',
        color: theme.palette.common.black,
    },
    body: {
        fontSize: 14,
    },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(odd)': {
            backgroundColor: theme.palette.action.hover,
        },
    },
}))(TableRow);



const useStyles = makeStyles({
    table: {
        minWidth: 300,
    },
});


let colors =[
    {name:"Info",value:"#81F4E5"},
    {name:"Potential",value:"#678EF3"},
    {name:"Low",value:"#EFF876"},
    {name:"Medium",value:"#03FA14"},
    {name:"High",value:"#FC6202"},
    {name:"Critical",value:"#FE0606"}
]



function CustomizedTables(props) {
    const classes = useStyles();
    useEffect(() => {
        props.signaturesActions()
    }, []);

    const [page, setPage] = React.useState(0);
    const [rowsPerPage, setRowsPerPage] = React.useState(10);

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
        setPage(0);
    };
    return (
        <>
            <h2>Signatures Summary</h2>
            <div style={{width:"auto", height:"3px" ,backgroundColor:"gray"}}></div>
            <TableContainer component={Paper}>
                <Table className={classes.table} aria-label="customized table">
                    <TableHead>
                        <TableRow>
                            <StyledTableCell>STT</StyledTableCell>
                            <StyledTableCell>SignatureID</StyledTableCell>
                            <StyledTableCell>Signature</StyledTableCell>
                            <StyledTableCell>Risk</StyledTableCell>
                            <StyledTableCell>Absolute Path</StyledTableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {props.dataAPI ?
                            props.dataAPI.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row,index) => (
                                <StyledTableRow>
                                    <StyledTableCell>{page * rowsPerPage + index + 1}</StyledTableCell>
                                    <StyledTableCell><Chip className={classes.chip} style={{backgroundColor:"#6cec94"}} label={row.SignID}/></StyledTableCell>
                                    <StyledTableCell>{row.Name}</StyledTableCell>
                                    <StyledTableCell><Chip className={classes.chip} style={{
                                        background:colors.find(elem => elem.name==row.Risk).value,
                                        color:"white"
                                    }} label={row.Risk}/></StyledTableCell>
                                    <StyledTableCell>{row.AsbPath}</StyledTableCell>
                                </StyledTableRow>
                            )):null}
                    </TableBody>
                </Table>
            </TableContainer>
            <TablePagination
                rowsPerPageOptions={[5, 10, 15]}
                component="div"
                count={props.dataAPI?props.dataAPI.length:0}
                rowsPerPage={rowsPerPage}
                page={page}
                onChangePage={handleChangePage}
                onChangeRowsPerPage={handleChangeRowsPerPage}
            />
            {
                props.loading?
                    <CircularProgress color="secondary" />
                    :null
            }
        </>
    );
}

const mapDispatchToProps = dispatch => {
    return {
        signaturesActions: () => dispatch(SignaturesAction.getSignatures()),
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        dataAPI:state.SignaturesReducer.data,
        loading:state.SignaturesReducer.loading
    }
}
const withConnect = connect(
    mapStateToProps,
    mapDispatchToProps
)

export default compose(withConnect)(CustomizedTables);