import React, {Component} from 'react';
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import {CardContent} from "@material-ui/core";

class AboutUs extends Component {
    render() {
        return (
            <div >
                <br/>
                <Card style={{width: "70%", marginLeft: "15%", height: "700"}}>
                    <CardHeader>

                    </CardHeader>
                    <CardContent>
                        <h1># About us</h1><br/>
                        <div style={{textAlign: "left"}}>
                            <div style={{width: "50%", height: "30%", marginLeft: "30%"}}>
                                We are currently the final year students of FPT University.<br/>
                                Our passion is developing security-related software<br/>
                                This product is used to scan for security vulnerabilities on the Web Application
                                platform
                            </div>
                        </div>
                        <br/><br/><br/>

                        <h1># Projects</h1><br/>
                        <div style={{textAlign: "left"}}>
                            <div style={{width: "50%", height: "30%", marginLeft: "30%"}}>
                                goSc4n- for automated Web Application Testing.
                                <br/><br/>
                                fuzzSc4n - for reconnaissance and vulnerability scanning. (Expected)
                            </div>
                        </div>
                        <br/><br/><br/>
                        <h1># Contact us</h1>
                        <span style={{color: "blue"}}><u>goSc4n_fpt@gmail.com</u></span>
                        <br/><br/><br/>
                        <h1># Support me</h1>
                        <br/>
                        <br/>
                        <div style={{textAlign: "left"}}>
                            <div style={{width: "50%", height: "30%", marginLeft: "30%"}}>
                                If you love my tools and would like to support my work, please send me an email.
                            </div>
                        </div>
                    </CardContent>
                    <CardHeader>

                    </CardHeader>
                </Card>
            </div>
        );
    }
}

export default AboutUs;